body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow: hidden;
}
.App {
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  height: 100vh;
  width: 100vw;
  background-image: url('/boat.jpg');
  background-size: cover;
  background-position: center;
  font-family: 'Palanquin', sans-serif;
  letter-spacing: 1px;
  color: rgb(219, 138, 100);

}
.current{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  z-index: 1;
  -webkit-justify-content: space-around;
          justify-content: space-around;;
  min-height: 50px;
  width: 40vw;
}
.current h4{
  font-size: 2vmax;
}
.current p{
  font-weight: 900;
  font-size: 1.5vmax;
}
p.current{
  font-size: 4vmax;
  color: aliceblue;
}
h6{
  position: fixed;
  bottom: 0;
  right: 10px;
  font-size: 15px;
}
h6.photo{
  left:10px;
  right: auto;
}
a
{
  color: beige;
}

.animation{
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.603);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center; 

}
.spinner{
  width: 75px;
  height: 75px;
  border-radius: 100%;
  border-width: 8px;
  border-color: transparent;
  border-style: solid;
  border-top-color: #B7A3DF;
  box-sizing: border-box;
  -webkit-animation: opacity 0.5s linear infinite;
          animation: opacity 0.5s linear infinite;
}

@-webkit-keyframes opacity {
    0%{
      opacity: 1;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    50%{
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
      opacity: 0.4;
    }
    100%{
      opacity: 1;
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

@keyframes opacity {
    0%{
      opacity: 1;
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    50%{
      -webkit-transform: rotate(180deg);
              transform: rotate(180deg);
      opacity: 0.4;
    }
    100%{
      opacity: 1;
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }      
  

.tiles{
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    background-color: rgba(88, 30, 122, 0.719);
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-end;
            align-items: flex-end;
}
.tile{
    text-align: center;
    border-radius: 3px;
    margin: 0 2px 70px 2px;
    background: rgba(41, 2, 41, 0.767);
    font-size: 1.2vmax;
  }
  p{
    padding: 5px 5px 0px 5px;
    font-size: 1vmax;
}
h4{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 20px;
}

@media(orientation:portrait){
    .tile{
        height: 220px;
        width: 45px;
        
    }
    canvas{
        height: 30px;
        width: 30px;
    }
    h4{
        font-size: 0.9vmax;
    }
    h6{
        font-size: 5px;
    }
    

}
@media(orientation:landscape){
    .tile{
        height: 150px;
        width: 70px;
        margin: 0 2px 30px 2px;

    }
    canvas{
        height: 30px;
        width: 30px;
    }
    h4{
        font-size: 0.9vmax;
    }
    h6{
        font-size: 5px;
    }

}
@media(min-width:1025px){
    .tile{
        height: 350px;
        width: 140px;
    }
    canvas{
        height: 60px;
        width: 60px;
    }
    h4{
        font-size: 0.9vmax;
    }
    h6{
        font-size: 15px;
    }

}
@media(max-width:320px){
    .tile{
        height: 200px;
        width: 42px;
    }
    canvas{
        height: 30px;
        width: 30px;
    }
    h4{
        font-size: 0.9vmax;
    }

}
